body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;background-color:#15202b;min-height:100vh}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.header{position:fixed;top:0;left:0;width:100%;display:flex;align-items:center;justify-content:center;background-color:#15202b;z-index:1}.header img{width:70px;margin-right:20px}.header h1{color:white}

.modal-container{display:flex;align-items:center;justify-content:center}

.form-send-tweet{background-color:#fff;border:2px solid #00acee;padding:20px;width:600px}.form-send-tweet__title{margin:0 !important}.form-send-tweet__form .MuiFormControl-root{width:100%}.form-send-tweet__form-name{width:50% !important}.form-send-tweet__form-textarea{width:100%}

.send-tweet__open-modal{position:fixed !important;right:30px;bottom:30px;background-color:#00acee !important;z-index:1}.send-tweet__close-modal{cursor:pointer;float:right;font-size:27px;width:30px;height:30px;display:flex;justify-content:center;align-items:center}

.tweet{position:relative}.tweet__header{display:flex;align-items:center;justify-content:space-between}.tweet__header h5{margin:0}.tweet__header svg:hover{cursor:pointer;fill:#f00}.tweet__date-add-tweet{bottom:5px;right:5px;font-size:10px}

.list-tweets-empty{margin-top:150px !important;text-align:center;color:#fff}.list-tweets{margin-top:150px !important}

